export const COLOR_MODE_KEY = 'color-mode';

export const COLORS_RANGE = ['#F583BA', '#6DDAF2', '#92E085', '#F5BD69', '#F2856D'];

export const COLORS_VIBANT = ['#70d5fc', '#f16da5', '#f7966f', '#ffd670', '#e6f06e'];

const colors = COLORS_VIBANT;

const sizes = [3, 4.5, 6, 7.5, 9];

export function random(list) {
  const random = Math.floor(Math.random() * list.length)
  return list[random];
}

export const randomColor = () => random(colors)
export const randomSize = () => random(sizes)